.page:has(.page__menu:target) {
  overflow: hidden;
}

.menu {
  overflow: auto;

  box-sizing: border-box;
  height: 100vh;
  background-color: #e5e6e8;
  font-weight: 700;
  z-index: 1;

  &__top {
    position: sticky; 
    top: 0;
    z-index: 1;
    background-color: #e5e5e5;

    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 22.4px;
    color: $main-text-color;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content;

    padding-bottom: 4px;
    border-bottom: 1px solid $main-text-color;

    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    color: $main-text-color;
    text-decoration: none;
  }
}
